import { 
  EDIT_FLEET,
  FETCH_FLEET,
  FETCH_FLEET_TO_DISTRIBUTION_CENTER
} from './FleetActions'

const INITIAL_STATE = {
  items: [],
  roles: [],
  totalSize: 0,
  page: 1,
  sizePerPage: 10,
  filters: [],
  editingFleet: null,
}

export default (state = INITIAL_STATE, action) => {
  switch (action.type) {
    case FETCH_FLEET.SUCCESS:
      return {
        ...state,
        items: action.payload.data.data.items,
        filters: action.payload.filters,
      }
    case EDIT_FLEET.SUCCESS:
      return {
        ...state,
        editingFleet: action.payload,
      }
    case 'FLEET_INITIALIZE':
      return {
        ...state,
        roles: action.payload.data.roles,
        stores: action.payload.data.stores,
        branches: action.payload.data.branches,
        role: action.payload.data.role,
      }
    default:
      return state
  }
}

export const getFleetListData = state => {
  return {
    items: state.fleet.items,
  }
}

export const getEditingFleet = state => {
  return state.fleet.editingFleet
}
